import { useEffect , useState}  from 'react';
import st from './login.module.scss';
import cl from 'classnames';
import { Button } from '../../components';
import { FiLogIn , FiUser , FiLock} from 'react-icons/fi';
import Logo from '../../assets/images/logo.png';

function Login(){

    const [username , setUsername] = useState('');
    const [password , setPassword] = useState('');
    const [load , setLoad] = useState(false);
    const [error , setError] = useState(false);
    return(
        <div className={st.login}>
            <div className={cl(st.login_card)}>
                <h1>Avtorizaciya</h1>
                <form onSubmit={ e => {
                    e.preventDefault();
                    if(password === "12345" && username === "gm_admin"){
                        localStorage.setItem("user","token");
                        window.location.reload();
                    }else{
                        setError(true);
                    }
                }}>
                    <div className="form-group my-3">
                        <label htmlFor="login"><FiUser/> Foydalanuvchi nomi</label>
                        <input required type="text" onChange={ e => setUsername(e.target.value)} id="login" className="form-control"/>
                    </div>
                    <div className="form-group my-3">
                        <label htmlFor="login"><FiLock/> Parol</label>
                        <input required type="password" id="login" onChange={ e => setPassword(e.target.value)} className="form-control"/>
                    </div>
                    {
                        error &&
                        <div className="alert alert-danger">
                            <b>Xatolik :</b> Login yoki parol noto'g'ri !
                        </div>
                    }
                    <br/>
                    <Button icon={<FiLogIn/>} htmlType="submit" iconAlign="left" block={true} title="Kirish"/>
                </form>
            </div>
        </div>
    )
}

export default Login;