import './products.scss';
import { FiPlus } from 'react-icons/fi';
import { Link } from 'react-router-dom';
import  React , { useEffect , useState} from 'react';
import { useDispatch , useSelector } from 'react-redux';
import { ProductActions } from '../../redux/actions';
import { Switch } from '../../components';
import { v4 } from 'uuid';
import apis from '../../api';


function Products(){

    const [list,  setList] = useState([]);

    const dispatch = useDispatch();
    const products = useSelector(({products}) => (products))

    useEffect(()=>{
        dispatch(ProductActions.getProducts())
    },[])

    useEffect(() => {
        const items = [];
        products.list.forEach( item => {
            const { title , image , price , is_available , id} = item;
            items.push({title , image , price , is_available , id})
        })
        setList([].concat(items))
    },[products])


    return(
        <>
            <Link className="addProductButton" to='/products/add'> <FiPlus/> </Link>
            <DataTableForProducts items={list}/>
        </>
    )
}

function DataTableForProducts({items}){
    const dispatch = useDispatch();

    return(
        <div className="table">
            <div className="header">
                <div>  </div>
                <div> Nomi </div>
                <div> Rasmi </div>
                <div> Narxi </div>
                <div> Mavjudligi </div>
                <div> O'chirish </div>
            </div>
            {
                items.length > 0 ? items.map((item,index)=>(<Product onDelete={()=>dispatch(ProductActions.getProducts())} onUpdate={()=>{ dispatch(ProductActions.getProducts()) }} item={item} index={index} key={v4()}/>))
                    : <h1 className="nodata"> Hech qanday mahsulotlar qo'shilmagan </h1>
            }
        </div>
    )
}



function Product({item , index , onUpdate , onDelete}){

    const [available , setAvailable] = useState(item.is_available);
    const [load , setLoad] = useState(false);
    const [deLoad , setDeLoad] = useState(false)

    const update = (value , id) => {
        setLoad(true);
        const data = new FormData();
        data.append('is_available' , value);
        apis.productAPI.toggleProduct(data , id)
            .then(()=>{
                onUpdate()
            })
            .catch( err =>{
                console.log(err)
            })
            .finally(()=>setLoad(false))
    }

    useEffect(()=>{
        setAvailable(item.is_available)
    },[item])

    const deleteProduct = id => {
        setDeLoad(true);
        apis.productAPI.deleteProduct(id)
            .then(()=>{
                onDelete()
            })
            .catch(err =>{
                console.log(err)
            })
            .finally(()=>setDeLoad(false))
    }

    return(
        <div className="body">
            <div> {index+1} </div>
            <div> {item.title.uz} </div>
            <div> <img src={`http://app.partsmart.uz:8001/files/${item.image}`} alt={`${item.image}`}/> </div>
            <div> ${item.price} SUM</div>
            <div>  { load ? "Kuting..." : <Switch value={available} onToggle={val => update(val , item.id)}/> } </div>
            <div> 
               {
                   deLoad ? "Kuting" :  <button onClick={()=>deleteProduct(item.id)}>O'chirish</button>
               }
            </div>
        </div>
    )
}


export default Products;