import { createReducer } from '@reduxjs/toolkit';
import { CategoryActions } from '../actions'

const initialState = {
    brands : {
        pending : true,
        rejected : false ,
        list : []
    },
    autos : {
        pending : true,
        rejected : false ,
        list : []
    },
    categories : {
        pending : true,
        rejected : false ,
        list : []
    }
}

const categoryReducer = createReducer(initialState , builder => {
    builder
        .addCase( CategoryActions.getBrands.fulfilled , (state,action) =>{
            return {...state , brands : { ...state.brands , pending : false , list : [].concat(action.payload)}}
        })
        .addCase( CategoryActions.getCategories.fulfilled , (state,action) => {
            return {...state , categories : { pending : false , rejected : false , list : action.payload }}
        })
        .addCase( CategoryActions.getCarModels.fulfilled , (state , action) => {
            return { ...state , autos : { pending : false , rejected : false , list : action.payload }}
        })
})

export default categoryReducer;