import React , { useEffect, useState } from 'react';
import './addCategory.scss';
import {  Loader , Input , Select , Button} from '../../../components';
import api from '../../../api';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { AiOutlinePlus } from 'react-icons/ai';

function AddCarModel({isLoad , brends}){

    const [brands , setBrands] = useState(brends);
    const [load , setLoad] = useState(false);
    const [success , setSuccess] = useState(false);

    const carmodelValidator = Yup.object({
        name : Yup.string().required('Avtomobil nomini kiriting . ').min(3,'Avtomobil nomi kamida 3 ta belgidan iborat b\'lishi shart .'),
        brend : Yup.string().required("Brendni tanlang").notOneOf(["-1"] , "Brendni tanlang."),
        years : Yup.string().required("Yil(lar)ni kiriting . ").min(4,"Kamida bir yilni kiriting .")
    })

    useEffect(()=>{
        setBrands(brends);
    },[brends])

    if(isLoad){
        return <Loader/>
    }

    function CraeteCarModel(values , reset ) {
        setLoad(true);
        setSuccess(false);
        const { name , years , brend} = values;
        api.categoryAPI.createCarModel({name , years : years.split(',').map( year => (+year)) , brend})
            .then( res =>{
                setSuccess(true);
                reset();
            })
            .catch( e =>{
                console.log(e)
            })
            .finally(()=>{
                setLoad(false);
            })
    }

    return(
        <Formik validationSchema={carmodelValidator}
            onSubmit={(values,formik) => CraeteCarModel(values,formik.resetForm )}
            initialValues={{
                name : '',
                brend : "-1",
                years : ''
            }}>
                {
                    (fProps => (
                        <div className="panel">
                            <h4>Avtomobil qo'shish</h4>
                            <div className="padded">
                                <div className="form">
                                    <Input error={fProps.touched.name && fProps.errors.name} value={fProps.values.name} name='name' label="Avtomobil nomi"
                                      handleChange={fProps.handleChange}/>
                                    <Select label="Brendni tanlang" defaultVal={fProps.values.brend}
                                        name="years" handleChange={value => fProps.setFieldValue('brend',value)}
                                        error={fProps.touched.brend && fProps.errors.brend} items={brands}/>
                                </div>
                                <Input error={fProps.touched.years && fProps.errors.years} value={fProps.values.years} type="text" name="years"
                                    handleChange={fProps.handleChange} label="Ehtiyot qismlar mavjud yillar (vergul bilan)"/>
                                {
                                    success && <Message duration={1500} message="Avtomobil muvoffaqiyatli qo'shildi."/>
                                }
                                <Button disabled={load} className="submit" title="Qo'shish" type="primary"
                                    icon={<AiOutlinePlus/>} iconAlign={"left"}
                                    onClick={fProps.handleSubmit} />
                            </div>
                        </div>
                    ))
                }
        </Formik>
    )
}


function Message({duration = 2000 , message}){
    const [visible,setVisible] = useState(true);

    useEffect(()=>{
        setTimeout(()=>{
            setVisible(false)
        },duration)
    },[])

    if(visible){
        return(
            <p className="message">
                { message }
            </p>
        )
    }else{
        return <></>
    }
}


export default AddCarModel;