import axios from 'axios';

const apiURL = 'http://app.partsmart.uz:8001/api/v1/slider/';

const sliderApi = {
    createSlider : data => (axios.post(`${apiURL}create`,data)),
    getSlides : () => (axios.get(`${apiURL}list`)),
    deleteSlide : id => (axios.delete(`${apiURL}delete/${id}`))
}


export default sliderApi;