import { Login, Stats , Products, AddProduct , CategoryControl , Reklama} from './pages';
import './App.scss';
import './fonts.css';
import { Header } from './components';
import 'bootstrap/dist/js/bootstrap';
import { Route , Switch } from 'react-router-dom';

function App(){
  if(localStorage.getItem("user")){
    return(
      <div>
        <Header>
          <Switch>
            <Route exact path='/' component={ Stats }/>
            <Route exact path='/orders' render={()=>{
              return(
                <div className="alert alert-primary" role="alert">
                  <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Esse, culpa ea sequi illo numquam maxime vitae eos dignissimos maiores inventore vel velit sed explicabo pariatur expedita recusandae a nulla nam?</p>
                </div>
              )
            }}/>
            <Route exact path='/products' component={Products}/>
            <Route exact path='/products/add' component={AddProduct}/>
            <Route exact path='/categories' component={CategoryControl}/>
            <Route exact path='/ads' component={Reklama}/>
            </Switch>
          </Header>
      </div>
    )
  }else{
    return(
      <Login/>
    )
  }
}
export default App;