import './stats.css';
import { CSSTransition , TransitionGroup } from 'react-transition-group';
import { useEffect, useState } from 'react';
import { v4 } from 'uuid';

function Stats() {

    const [items , setItems] = useState([
        { name : "John" , age : 43 , _id : v4() },
        { name : "Jane" , age : 23 , _id  :v4() },
        { name : "Bill" , age : 65 , _id : v4() },
        { name : "Tom" , age : 47 , _id : v4() }
    ])

    const [inProp, setInProp] = useState(false);
    return (
      <div>
          <TransitionGroup className="list">
                  {
                      items.map((item,index)=>{
                          return(
                              <CSSTransition key={index} classNames="fade" timeout={400}>
                                  <div className="item">
                                    <button onClick={ () => setItems( prev => prev.filter( person => person._id !== item._id && person))} >&times;</button>  {item.name} and her age {item.age}
                                  </div>
                              </CSSTransition>
                          )
                      })
                  }
          </TransitionGroup>
          <button onClick={()=> {
              const name = v4();
              const age = 19;
              setItems( prev => [...prev , { name : name , age : age , _id : v4() }])
              window.scrollTo({top : document.documentElement.scrollHeight, left : 0})
          }} className="add">Add User</button>
      </div>
    );
  }
export default Stats;