import { createAsyncThunk } from '@reduxjs/toolkit'
import api from  '../../api';


const categoryActions = {
    getBrands : createAsyncThunk('categories/getBrands' , async () => {
        const response = await api.categoryAPI.getBrand();
        return response.data;
    }),
    getCategories : createAsyncThunk('categories/getCategories', async () =>{
        const response = await api.categoryAPI.getCategody();
        return response.data
    }),
    getCarModels : createAsyncThunk('categories/getCarModels' , async (id) => {
        const response = await api.categoryAPI.getCarModel(id);
        return response.data;
    })
}

export default categoryActions;