import React , { useEffect, useState } from 'react';
import './category.scss';
import { Input , Button} from '../../../components';
import api from '../../../api';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { AiOutlinePlus } from 'react-icons/ai';

function AddCategory(){

    const [load , setLoad] = useState(false)
    const [success , setSuccess] = useState(false);

    const carmodelValidator = Yup.object({
        name_uz : Yup.string().required('Kategoriya nomini kiriting . ').min(3,'Kategoriya nomi kamida 3 ta belgidan iborat b\'lishi shart .'),
        name_ru : Yup.string().required('Kategoriya nomini kiriting . ').min(3,'Kategoriya nomi kamida 3 ta belgidan iborat b\'lishi shart .')
    })

    const createCategory = (values,  reset) => {
        setLoad(true);
        api.categoryAPI.createCategory({
            name : {
                uz : values.name_uz,
                ru : values.name_ru
            }
        })
        .then( res =>{
            setSuccess(true);
            reset();
            console.log(res.data)
        })
        .catch( e => {
            console.log(e)
        })
        .finally(()=>{
            setLoad(false)
        })
    }

    return(
        <Formik validationSchema={carmodelValidator}
            onSubmit={(values, formik) => createCategory(values , formik.resetForm)}
            initialValues={{
                name_uz : '',
                name_ru : ''
            }}>
                {
                    (fProps => (
                        <div className="panel">
                            <h4>Kategoriya</h4>
                            <div className="padded">
                                <div className="form">
                                    <Input value={fProps.values.name_ru} name='name_ru'
                                    error={fProps.touched.name_ru && fProps.errors.name_ru} label="Kategoriya nomi (rus)" handleChange={fProps.handleChange}/>
                                    <Input value={fProps.values.name_uz} name='name_uz' 
                                    error={fProps.touched.name_uz && fProps.errors.name_uz} label="Kategoriya nomi (uz)" handleChange={fProps.handleChange}/>
                                </div>
                                {
                                    success && <Message hider={setSuccess} duration={1500} message="Kategoriya muvoffaqiyatli yaratildi."/>
                                }
                                <Button disabled={load} className="submit" title="Qo'shish" type="primary"
                                    icon={<AiOutlinePlus/>} iconAlign={"left"}
                                    onClick={fProps.handleSubmit} />
                            </div>
                        </div>
                    ))
                }
        </Formik>
    )
}


function Message({duration = 2000 , message ,hider}){
    const [visible,setVisible] = useState(true);

    useEffect(()=>{
        setTimeout(()=>{
            setVisible(false)
            hider(false)
        },duration)
    },[])
    if(visible){
        return(
            <p className="message">
                { message }
            </p>
        )
    }else{
        return <></>
    }
}

export default AddCategory;