import React from 'react';
import './switch.scss';
import  { v4 } from  'uuid'

export default function Switch({value , id = v4() ,  onToggle = (val) => console.log(val)}) {


    return(
        <label htmlFor={id} className="switch">
            <input checked={value} id={id} type="checkbox" onChange={ (e)=> onToggle(e.currentTarget.checked)}/>
            <span className="slider round"></span>
        </label>
    )
}