import axios from 'axios';

const apiURL = 'http://app.partsmart.uz:8001/api/v1/menu/';

const category = {
    createBrand : data => (axios.post(`${apiURL}brand/create/` , data )),
    getBrand : () => (axios.get(`${apiURL}brand/list/`)),

    createCategory : data => (axios.post(`${apiURL}device-type/create`,data)),
    getCategody : () => (axios.get(`${apiURL}device-type/list`)),

    createCarModel : (data) => (axios.post(`${apiURL}car-model/create/${data.brend}` , {
        name : data.name,
        years : data.years
    })),
    getCarModel : id => (axios.get(`${apiURL}car-model/list/${id}`))
}

export default category;
