import axios from 'axios';

const apiURL = 'http://app.partsmart.uz:8001/api/v1/product/';


const productAPI = {
    createProduct : data => (axios.post(`${apiURL}create`,data)),
    getProducts : (filters = null) => {
        let qs = "list?";
        if(filters){
            Object.keys(filters)
                .forEach( (key,index , fss) =>{
                    qs = qs+`${key}=${filters[key]}${index+1 !==fss.length ? "&" : ""}`
                })
        }
        return axios.get(`${apiURL}${qs}`)
    },
    toggleProduct : (data,id) => (axios.put(`${apiURL}update/${id}`,data)),
    deleteProduct : id => (axios.delete(`${apiURL}delete/${id}`))
}
export default productAPI