import st from './sidebar.module.scss';
import cl from 'classnames';
import React , { useState , useEffect } from 'react';
import { NavLink } from 'react-router-dom';
import { FiUsers , FiPackage , FiShoppingCart  , FiPieChart , FiLayers ,FiX } from 'react-icons/fi';
import { AiOutlineNotification } from 'react-icons/ai'
import Waves from 'node-waves';
import { Button } from '../';

function Sidebar({open , onChange}){

    const [isOpen , setIsOpen] = useState(open);
    useEffect(()=>{
        setIsOpen(open)
    },[open])
    useEffect(()=>{
        Waves.init();
        Waves.attach('.closer','waves-light')
    },[])

    return(
        <>
            <div className={cl(st.sidebar , isOpen && st.sidebar_open )}>
                <ul className={cl(st.sidebar_menu,"sbmenu")}>
                    <Button onClick={()=>{
                        setIsOpen(!isOpen);
                        onChange(!isOpen)
                    }} className={cl(st.sidebar_toggler,"closer")} flat={true} icon={<FiX/>} type="flat"/>
                    <li>
                        <NavLink data-tooltip='Dashbord' exact to='/' className={st.sidebar_menu_item} activeClassName={st.sidebar_menu_item_active}>
                            <FiPieChart/>  <span>Dashbord</span>
                        </NavLink>
                    </li>
                    <li>
                        <NavLink  data-tooltip='Kategoriyalar' to='/categories' className={st.sidebar_menu_item} activeClassName={st.sidebar_menu_item_active}>
                            <FiLayers/>  <span>kategoriyalar</span>
                        </NavLink>
                    </li>
                    <li>
                        <NavLink data-tooltip='Mahsulotlar' to='/products' className={st.sidebar_menu_item} activeClassName={st.sidebar_menu_item_active}>
                            <FiPackage/>  <span>Mahsulotlar</span>
                        </NavLink>
                    </li>
                    <li>
                        <NavLink data-tooltip='Buyurtmalar' to='/orders' className={st.sidebar_menu_item} activeClassName={st.sidebar_menu_item_active}>
                            <FiShoppingCart/>  <span> Buyurtmalar</span>
                        </NavLink>
                    </li>
                    <li>
                        <NavLink data-tooltip='Foydalanuvchilar' to='/users' className={st.sidebar_menu_item} activeClassName={st.sidebar_menu_item_active}>
                            <FiUsers/>  <span> Foydalanuvchilar</span>
                        </NavLink>
                    </li>
                    
                    <li>
                        <NavLink data-tooltip='Reklamalar' to='/ads' className={st.sidebar_menu_item} activeClassName={st.sidebar_menu_item_active}>
                            <AiOutlineNotification/>  <span> Reklamalar</span>
                        </NavLink>
                    </li>
                </ul>
            </div>
            <div onClick={() =>{
                setIsOpen(!isOpen)
                onChange(!isOpen)
                }} className={cl(st.sidebar_overlay , isOpen && st.sidebar_visible)}></div>
        </>
        )
}

export default Sidebar;