import React, { useEffect } from 'react';
import './message.scss';

export default function Message({duration = 2000 , type = 'success', message = ""}) {
    const [visible,setVisible] = React.useState(true);
    useEffect(()=>{
        setTimeout(()=>{
            setVisible(false)
        },duration)
    },[])

    return(
        <div className={`messageContainer ${type} ${visible ? 'visible' : ''}`}>
            <p>
                { message }
            </p>
        </div>
    )
}