import React , { createRef, useEffect, useState } from 'react';
import { Button , Select, Input , Message} from '../../../components';
import './addProduct.scss';
import { AiOutlinePlus , AiOutlineDelete , AiOutlineDown} from 'react-icons/ai'
import {  Link } from 'react-router-dom';
import Swal from 'sweetalert2';
import upload from '../../../assets/images/upload.svg';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { connect , useDispatch , useSelector } from 'react-redux';
import CategoryActions from '../../../redux/actions/categoryActions';
import apis from '../../../api'

const reqd = "Majburiy maydon";
const miner = min => (`Bu maydon kamida ${min} belgidan iborat bo'lishi shart`)
const SUPPORTED_FORMATS = [
    "image/jpg",
    "image/jpeg",
    "image/gif",
    "image/png"
  ];


const seoValidate = Yup.object({
    title_uz : Yup.string().required(reqd).min(3,miner(3)),
    title_ru : Yup.string().required(reqd).min(3,miner(3)),
    desc_uz : Yup.string().required(reqd).min(3,miner(3)),
    desc_ru : Yup.string().required(reqd).min(3,miner(3)),
    keywords : Yup.string().required(reqd).min(3 ,miner(3)),


    gtitle_uz : Yup.string().required(reqd).min(3,miner(3)),
    gtitle_ru : Yup.string().required(reqd).min(3,miner(3)),
    part_number : Yup.string().required(reqd),
    category_id : Yup.string().required(reqd),
    brand_id : Yup.string().required(reqd),
    car_id : Yup.string().required(reqd),
    year : Yup.string().required(reqd),
    price : Yup.string().required(reqd),
    unique : Yup.string().required(reqd),
    is_available : Yup.boolean().required(reqd).typeError('Majburiy maydon'),
    file : Yup.mixed().required(reqd)
    .test('fileType' , "Noto'g'ri fayl tanlandi . Iltimos rasm tanlang (*.jpg , *.jpeg , *.gif , *.png formatlarida). " , value => value && SUPPORTED_FORMATS.includes(value.type))
})

function AddProduct(props){
    const [brands, setBrands] = useState([]);
    const [categories , setCategories] = useState([]);
    const [models , setModels] = useState([]);
    const [years , setYears] = useState([]);
    const categoriesList = useSelector( state => state.category);
    const dispatch = useDispatch();

    useEffect( ()=>{
        dispatch(CategoryActions.getBrands());
        dispatch(CategoryActions.getCategories());
    },[])

    useEffect(()=>{
        let brandList = [] , categoryList = [];
        categoriesList.brands.list.forEach( brand =>{
            brandList.push({
                value : brand.id,
                label : brand.name
            })
        })
        categoriesList.categories.list.forEach( category => {
            categoryList.push({
                value : category.id,
                label : category.name.uz
            })
        })
        if(categoriesList.autos.list !== null){
            let autos = [];
            categoriesList.autos.list.forEach( car => {
                autos.push({
                    value : car.id,
                    label : car.name
                })
                setModels([].concat(autos));
            })
        }else{
            setModels([].concat([]))
        }
        setCategories([].concat(categoryList));
        setBrands([].concat(brandList));
    },[categoriesList])
    

    function getCars (id) {
        dispatch(CategoryActions.getCarModels(id));
    }

    function getYears (val) {
        let isHave = true;
        categoriesList.autos.list.forEach( car =>{
            if(car.id === Number(val)){
                isHave = false;
                let yersList = [];
                car.years.forEach( year => {
                    yersList.push({
                        label : year,
                        value : year
                    })
                })
                setYears([].concat(yersList))
            }
        })

        if(isHave) setYears([]);
    }


    return(
        <div className="page">
            <BreadCrumb/>
            <General years={years} categories={categories} brands={brands} models={models}
                getYears={val => getYears(val)} getCars={ modelID => getCars(modelID) }/>
        </div>    
    )
}

function General({categories = [], brands = [] , models = [] , years = [] ,getCars , getYears , embedGeneral , embedParts , embedSEO}){

    const [image , setImage] = useState("");
    const [coords , setCoords] = useState([])
    const [parts , setParts] = useState([]);
    const preview = createRef();
    const [load , setLoad] = useState(false);
    const [success , setSuccess] = useState(false);
    const [error , setError] = useState(false);

    const createImage = file => {
        const Reader = new FileReader();

        Reader.onloadend = e => {
            setImage(e.target.result);
        }

        Reader.readAsDataURL(file);
    }

    function makeCoord(x,y){
        if(image !== ""){
            Swal.fire({
                title : "Bu qaysi son?",  
                text : "Rasmdagi sonni kiriting",
                input : "number",
                allowOutsideClick : false,
                preConfirm : (text) =>{
                    const select = document.createElement("span");
                    select.className = "select";
                    select.style.left = `${x-7}px`;
                    select.style.top = `${y-7}px`;
                    select.textContent = text;
                    select.id = `coord${text}`
                    preview.current.appendChild(select);

                    let prevCoords = coords;
                    if(prevCoords.length === 0){
                        setParts([].concat({ coord : [{x : x , y : y}] , value : text , info : {
                            title : {
                                uz : "",
                                ru : ""
                            },
                            price : "0",
                            part_number : "",
                            qt : "",
                            date : Date.now()
                        }}));
                        prevCoords = [{ coords : [{x : x , y : y}] , value : text }];
                        setCoords(prevCoords)
                    }else{
                        let isHave = prevCoords.find( item => item.value === text && item );
                        if(isHave){
                            prevCoords.forEach( item =>{
                                if(item.value === text){
                                    item.coords.push({x : x , y : y})
                                    let prevs = parts.find( part => part.value === item.value);
                                    prevs.coord.push({ x : x , y : y });

                                    let prevParts = parts;
                                    prevParts.forEach((part , index) => {
                                        if(part.value === item.value){
                                            prevParts[index] = prevs;
                                        }
                                    })

                                    setParts([].concat(prevParts))
                                }
                                setCoords([].concat(prevCoords));
                            })
                        }else{
                            prevCoords.push({ coords : [{x : x , y : y}] , value : text })
                            let prev = parts;
                            prev.push({ coord : [{x : x , y : y}] , value : text , info : {
                                title : {
                                    uz : "",
                                    ru : ""
                                },
                                price : "0",
                                part_number : "",
                                qt : "",
                                date : Date.now()
                            }})
                            setParts([].concat(prev));
                        }
                        setCoords([].concat(prevCoords))
                    }
                }
            })
        }
    }

    function createProduct(values){
        setLoad(true);
        setError(false);
        setSuccess(false);
        const { title_uz , title_ru , desc_uz , desc_ru , keywords ,gtitle_uz , gtitle_ru , part_number , category_id , brand_id , car_id , year , price , unique , file , is_available} = values;
        const seo = JSON.stringify({
            title : {
                uz : title_uz,
                ru : title_ru
            },
            desc : {
                uz : desc_uz,
                ru : desc_ru
            },
            keywords : keywords.split(',')
        })

        const title = JSON.stringify({
            uz : gtitle_uz,
            ru : gtitle_ru
        })
        const data = new FormData();
        data.append('title',title);
        data.append('parts', JSON.stringify(parts));
        data.append('part_number', part_number);
        data.append('price' , price);
        data.append('year', year)
        data.append('brand_id' , brand_id);
        data.append('car_model_id',car_id);
        data.append('device_type_id' , category_id);
        data.append('image', file);
        data.append('unique_number',unique);
        data.append('is_available',is_available);
        data.append('seo',seo);

        apis.productAPI.createProduct(data)
            .then( res => {
                setSuccess(true);
            })
            .catch( error =>{
                console.log(error)
                setError(true)
            })
            .finally( () => {
                setLoad(false)
            })
    }

    const deletePart = ipartID => {
        const deleted = coords.filter( coord => coord.value !== ipartID && coord)
        setCoords([].concat(deleted));
        document.querySelectorAll(`#coord${ipartID}`)
            .forEach( span => {
                span.remove();
            })
        const deletedInps = parts.filter( part => part.value !== ipartID && part);
        setParts([].concat(deletedInps));
    }

    return(
            <div className="panel">
                <div className="panel_section">
                    <div className="padded">
                        <Formik 
                            onSubmit={values => createProduct(values)} initialValues={{
                                gtitle_uz : '',
                                gtitle_ru : '',
                                part_number : undefined,
                                category_id : undefined,
                                brand_id : undefined,
                                car_id : undefined,
                                year : undefined,
                                price : undefined,
                                unique : undefined,
                                file : undefined,
                                is_available : null,

                                title_uz : "",
                                title_ru : "",
                                desc_uz : "",
                                desc_ru : "",
                                keywords : "",
                            }} validationSchema={seoValidate}>
                            { ({values , touched , errors , handleChange , handleSubmit , setFieldValue}) => {
                                return(
                                    <>
                                        <h4>Asosiy</h4>
                                        <div className="form">
                                            <Input value={values.gtitle_uz}  type="text" label="Nomi (uz)" name='gtitle_uz'
                                                error={touched.gtitle_uz && errors.gtitle_uz} handleChange={handleChange}/>
                                            <Input type="text" label="Nomi (ru)" name='gtitle_ru'
                                                error={touched.gtitle_ru && errors.gtitle_ru } handleChange={handleChange}/>
                                            <Input type="text" label="Partiya raqami" name='part_number'
                                                error={touched.part_number && errors.part_number} handleChange={handleChange}/>
                                            <Select items={categories} label="Kategoriyasi" name='category_id'
                                                error={touched.category_id && errors.category_id} handleChange={va => setFieldValue('category_id' , va)}/>
                                            <Select items={brands} label="Brend" handleChange={ e => {
                                                getCars(e)
                                                setFieldValue('brand_id' , e)
                                            }} name='brand_id'
                                                error={touched.brand_id && errors.brand_id} />
                                            <Select items={models} label="Avtomobil modeli" handleChange={ val => {
                                                setFieldValue('car_id' , val)
                                                getYears(val)
                                            }} name='car_id'
                                                error={touched.car_id && errors.car_id}/>
                                            <Select items={years} label="Yili" name='year'
                                                error={touched.year && errors.year} handleChange={ val =>{
                                                    setFieldValue('year',val);
                                                }}/>
                                            <Input type="number" label="Narxi" name='price'
                                                error={touched.price && errors.price} handleChange={handleChange}/>
                                            <Input type="text" label="Unikal raqami" name='unique' handleChange={handleChange}
                                                error={touched.unique && errors.unique}/>
                                            <Input type="file" name='file' handleChange={ val => {
                                                createImage(val.target.files[0])
                                                setFieldValue('file', val.target.files[0])
                                            }} label="Rasmni tanlang"
                                                error={touched.file && errors.file}/>
                                        </div>
                                        <Select label="Mavjudligi" items={[{label : "Mavjud" , value : true} ,{ label : "Mavjud emas" , value : false }]}
                                            error={touched.is_available && errors.is_available} name='is_available'
                                            handleChange={val => setFieldValue('is_available' , val)}/>
                                        <h4>SEO uchun</h4>
                                        <div className="form">
                                            <Input name='title_uz' handleChange={handleChange} value={values.title_uz}
                                                error={touched.title_uz && errors.title_uz}  label="Sarlavha (uz)"/>
                                            <Input name='title_ru' handleChange={handleChange} value={values.title_ru}
                                                error={touched.title_ru && errors.title_ru} label="Sarlavha (ru)"/>
                                            <Input name='desc_uz' handleChange={handleChange} value={values.desc_uz}
                                                error={touched.desc_uz && errors.desc_uz} label="Ta'rif (uz)"/>
                                            <Input name='desc_ru' handleChange={handleChange} value={values.desc_ru}
                                                error={touched.desc_ru && errors.desc_ru} label="Ta'rif (ru)"/>
                                        </div>
                                        <Input name='keywords' handleChange={handleChange} value={values.keywords}
                                            error={touched.keywords && errors.keywords} label="Kalit so'zlar"/>
                                        <div className="panel_section">
                                            <h4>Qismlarini yaratish</h4>
                                                <div className="preview">
                                                    <div ref={preview} className="preview_image">
                                                        <img onClick={e => makeCoord(e.nativeEvent.offsetX , e.nativeEvent.offsetY)} className={`${image === "" ? "empty" : ""}`} src={image !== "" ? image : upload} alt=""/>
                                                    </div>
                                                    <div className="preview_values">
                                                        {
                                                            parts.map( part => {
                                                                return(
                                                                    <ProductParts onChange={values => {
                                                                        let updated = parts;
                                                                        updated.forEach( (up,index) => {
                                                                            if(up.value === values.value){
                                                                                updated[index] = values;
                                                                            }
                                                                        })
                                                                        setParts([].concat(updated));
                                                                    }} key={`partItem${part.value}`} onDelete={ (partVal) => deletePart(partVal)} part={part} item={part.value}/>
                                                                )
                                                            })
                                                        }
                                                    </div>
                                                </div>
                                        </div>
                                        {
                                            success && <Message message="Mahsulot muvoffaqiyatli qo'shildi ." duration={2000} type="success"/>
                                        }
                                        {
                                            error && <Message message="Mahsulotni qo'shishda xatolik . Iltimos barcha maydonlar to'g'ri to'ldirilganligini tekshirib chiqing ."
                                                type="error" duration={2300}/>
                                        }
                                        <Button disabled={load} title="Qo'shish" onClick={handleSubmit} type="primary" icon={<AiOutlinePlus/>}/>
                                    </>
                                )
                             }}
                        </Formik>
                    </div>
                </div>
            </div>
    )
}

function ProductParts({item , onDelete , part , onChange}){

    const [values , setValues] = useState([]);

    useEffect(()=>{
        setValues(part)
    },[])

    useEffect(()=>{
        onChange(values)
    },[values,part])

    return(
        <div className="productPart">
            <h4 className="productPart_num" data-toggle='collapse' data-target={`#part${item*32}`}> <span>{item}</span> 
                <div>
                    <button className="delete" onClick={()=>onDelete(part.value)}>
                        <AiOutlineDelete/>
                    </button>
                    <button className="delete">
                        <AiOutlineDown/>
                    </button>
                </div>
            </h4>
            <div className="collapse" id={`part${item*32}`}>
                <div className="panel">
                    <div className="padded">
                        <div className="form">
                            <Input label="Nomi (ru)" handleChange={val => setValues({...values , info : {
                                ...values.info,
                                title : {
                                    ...values.info.title,
                                    ru : val.target.value
                                }
                            }})}/>
                            <Input label="Nomi (uz)"  handleChange={val => setValues({...values , info : {
                                ...values.info,
                                title : {
                                    ...values.info.title,
                                    uz : val.target.value
                                }
                            }})}/>
                            <Input label="Partiya raqami" handleChange={ val => setValues({
                                ...values,
                                info : {
                                    ...values.info,
                                    part_number : val.target.value
                                }
                            }) }/>
                            <Input label="Komplektdagi soni" type="number"  handleChange={ val => setValues({
                                ...values,
                                info : {
                                    ...values.info,
                                    qt : val.target.value
                                }
                            }) }/>
                            <Input label="sana" type="date"  handleChange={ val => setValues({
                                ...values,
                                info : {
                                    ...values.info,
                                    date : val.target.value
                                }
                            }) }/>
                            <Input label="Narxi" type="number"  handleChange={ val => setValues({
                                ...values,
                                info : {
                                    ...values.info,
                                    price : val.target.value
                                }
                            }) }/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

function BreadCrumb(){
    return(
        <div className="breadcrumb">
            <Link className="breadcrumb-item" to='/'>Bosh sahifa</Link>
            <Link className="breadcrumb-item" to='/products'>Mahsulotlar</Link>
            <Link className="breadcrumb-item" to='/products/add'>Qo'shish</Link>
        </div>
    )
}



export default connect(null,null)(AddProduct)