import { useState } from 'react';
import './fileinput.scss';

function FileInput({onChange}){
    const [current , setCurrent] = useState("Tanlanmagan");

    return(
        <div className="fileinput">
            <label tabIndex={0} htmlFor="fileinput1234"> {current} </label>
            <input type="file" id="fileinput1234" onChange={ e =>{
                setCurrent(e.target.files[0].name);
                if(onChange){
                    onChange(e.target.files[0])
                }
            }}/>
        </div>
    )
}

export default FileInput;