import React , { useEffect, useState} from 'react';
import { Formik } from 'formik';
import * as Yup from 'yup';
import { Input ,Button} from '../../components'
import './categoryControl.scss';
import { Link } from 'react-router-dom';
import { AiOutlinePlus } from 'react-icons/ai';
import apis from '../../api';
import AddCar from './add/addCategory';
import AddCategory from './category/category';


function CategoryControl(props){
    const [brands , setBrands] = useState([]);
    const [brendLoad , setBrandLoad] = useState(true);

    useEffect(()=>{
        Update().then( list =>{
            setBrands(list)
            setBrandLoad(false);
        })
    },[])

    async function Update() {
        const brands = await getBrands(setBrandLoad);
        let replaced = [];

        brands.forEach( brend => {
            replaced.push({
                label : brend.name,
                value : brend.id
            })
        })
        return replaced;
    }

    async function updateList() {
        setBrandLoad(true)
        Update().then( list =>{
            setBrands(list)
            setBrandLoad(false)
        })
    }

    return(
        <div className="categoryControl">
            <div className="breadcrumb">
                <Link className="breadcrumb-item" to='/'>Bosh sahifa</Link>
                <Link className="breadcrumb-item" to='/categories'>Kategoriyalar</Link>
            </div>
            <BrendCreator onAdd={updateList}/>
            <AddCar isLoad={brendLoad} brends={brands}/>
            <AddCategory/>
        </div>
    )
}


function BrendCreator({onAdd}){
    const [load,setLoad] = useState(false);
    const [success , setSuccess] = useState(false)
    const [error , setError] = useState({
        occured : false,
        message : ''
    });
    const SUPPORTED_FORMATS = [
        "image/jpg",
        "image/jpeg",
        "image/gif",
        "image/png"
      ];

    const brendValidator = Yup.object({
        name : Yup.string().required('Brend nomini kiriting . ').min(3,'Brend nomi kamida 3 ta belgidan iborat b\'lishi shart .'),
        image : Yup.mixed().required("Rasmni kiriting .")
            .test('fileType' , "Noto'g'ri fayl tanlandi . Iltimos rasm tanlang (*.jpg , *.jpeg , *.gif , *.png formatlarida). " , value => value && SUPPORTED_FORMATS.includes(value.type))
    });


    async function addBrend(values , reset){
        setLoad(true);
        setSuccess(false);
        setError(false);

        const data = new FormData();
        data.append("name" , values.name);
        data.append("image" , values.image);
        try{
            const result = await apis.categoryAPI.createBrand(data);
            if(result.status !== 201){
                setError({occured : true , message : `Mahsulot qo'shishda xatolik . Xatolik kodi : ${result.status}` });
                throw new Error("Zapchast qo'shishda xatolik");
            }
            reset.resetForm()
            onAdd()
            setSuccess(true)
        }
        catch(err){
            console.log(err);
            setSuccess(false)
        }
        finally{
            setLoad(false);
        }
    }

    return(
        <Formik
            validationSchema={brendValidator}
            onSubmit={(values , handleReset) => addBrend(values,handleReset)}
            initialValues={{
                name : '',
                image : undefined
            }}>
                {
                    (properties) => (
                        <div className="panel">
                            <h4>Brend qo'shish</h4>
                            <div className="padded">
                                <div className="form">
                                    <Input  name='name' type='text' value={properties.values.name} label='Model nomi'
                                        handleChange={properties.handleChange}
                                        error={(properties.touched.name && properties.errors.name)}/>
                                    <Input type='file' name='image' label='Model rasmi (ikonka)'
                                        value={properties.values.image ? properties.values.image.fileName : ''}
                                        handleChange={ e => {
                                            properties.setFieldValue("image" , e.currentTarget.files[0])
                                        }}
                                        error={(properties.touched.image && properties.errors.image) && properties.errors.image}
                                        />
                                </div>

                                {
                                    success && <Message hider={setSuccess} duration={1500} message="Brend muvoffaqiyatli yaratildi"/>
                                }
                                {
                                    error.occured && 
                                    <p className="errorText">
                                        {error.message}
                                    </p>
                                }

                                <Button disabled={load} className="submit" title="Qo'shish" type="primary"
                                    icon={<AiOutlinePlus/>} iconAlign={"left"}
                                    onClick={properties.handleSubmit} />
                            </div>
                        </div>
                    )
                }
        </Formik>
    )
}

function Message({duration = 2000 , message , hider}){
    const [visible,setVisible] = useState(true);

    useEffect(()=>{
        setTimeout(()=>{
            setVisible(false)
            hider(false)
        },duration)
    },[])
    if(visible){
        return(
            <p className="message">
                { message }
            </p>
        )
    }else{
        return <></>
    }
}

async function getBrands () {
    try{
        const brands = await apis.categoryAPI.getBrand();
        if(brands.status !== 200){
            throw new Error(`Error occured while requesting to brends list . Error code : ${brands.status}`);
        }
        return brands.data;
    }
    catch(e){
        return e;
    }
}

export default CategoryControl;