import React , { useState , useEffect }from 'react';
import { Input , Select , Button , Message , Switch } from '../../components';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { AiOutlinePlus } from 'react-icons/ai'
import { useSelector , useDispatch } from 'react-redux';
import { SliderActions } from '../../redux/actions';
import './reklama.scss';
import { v4 } from 'uuid';
import api from '../../api';

export default function Reklama(){

    const [success , setSuccess] = useState(false);
    const [load , setLoad] = useState(false);
    const [failed , setFailed] = useState(false);
    const slides = useSelector(({slides}) => (slides));
    const dispatch = useDispatch();
    const [list , setList] = useState([])

    useEffect(()=>{
        dispatch(SliderActions.getSliders());
    },[])

    useEffect(()=>{
        setList(slides.list);
    },[slides])

    function createBanner(values) {
        const data = new FormData();
        Object.keys(values)
            .forEach( key =>{
                data.append(key , values[key])
            })
        setLoad(true);
        setFailed(false);
        setSuccess(false);

        api.sliderApi.createSlider(data)
            .then(()=>{
                setSuccess(true)
                dispatch(SliderActions.getSliders())
            }).catch((e) => {
                console.log(e);
                setFailed(true)
            })
            .finally(()=>{
                setLoad(false)
            })

    }

    const imageFileTypes = [
        "image/jpg",
        "image/jpeg",
        "image/png",
        "image/gif"
    ]

    const bannerValidators = Yup.object({
        image : Yup.mixed().required("Iltimos rasmni tanlang")
        .test('fileType' , "Noto'g'ri fayl tanlandi . Iltimos rasm tanlang (*.jpg , *.jpeg , *.gif , *.png formatlarida). " , value => value && imageFileTypes.includes(value.type)),
        url : Yup.string().url("Noto'g'ri URL manzili kiritildi").min(10,"URL manzili uzunligi kamida 10 simvoldan iborat bo'lishi kerak . ").required("Iltimos URL manzilini kiriting"),
        status : Yup.boolean().required("Reklama statusini tanlang...")
    })

    return(
        <div className="reklama">
            <div className="banner">
                <div className="panel">
                    <h4>Banner yaratish</h4>
                    <div className="padded">
                        <Formik onSubmit={values => createBanner(values)}
                            initialValues={{ status : undefined , url : "" , image : undefined }} validationSchema={bannerValidators}>
                            {
                                ({values , handleChange , handleSubmit , touched , errors , setFieldValue}) => {
                                    return(
                                        <>
                                            <div className="form">
                                                <Input type="text" label="URL manzilini kiriting"
                                                    value={values.url} name="url" handleChange={handleChange}
                                                    error={touched.url && errors.url}/>
                                                <Input type="file" label="Rasmni tanlang"
                                                    value={values.image ? values.image.fileName : ""} name="url" handleChange={ e => setFieldValue('image' , e.target.files[0])}
                                                    error={touched.image && errors.image}/>
                                            </div>
                                            <Select error={touched.status && errors.status} defaultVal={true} items={[{label : "Ko'rsatish" , value : true},
                                                {label : "Yashirish" , value : false}]} name="status"
                                                handleChange={val => setFieldValue('status' , val)}/>
                                                {
                                                    success && <Message type="success" duration={2000} message="Banner muvoffaqiyatli qo'shildi"/>
                                                }
                                                {
                                                    failed && <Message type="error" duration={2500} message="Bannerni qo'shishda xatolik sodir bo'ldi . Developerga murojaat qiling . "/>
                                                }
                                            <Button disabled={load} title="Qo'shish" icon={<AiOutlinePlus/>} type="primary" onClick={handleSubmit}/>
                                        </>
                                    )
                                }
                            }
                        </Formik>

                    </div>
                </div>
            </div>
            <SlidesList items={list}/>
        </div>
    )
}

function SlidesList({items}){
    return(
        <div className="slidesList">
            <div className="header">
                <div>  </div>
                <div>Rasm</div>
                <div> URL </div>
                <div> Status </div>
                <div> O'chirish </div>
            </div>
            {
                items.length>0 ? items.map((item,index)=>{
                    return(
                        <ListItem item={item} key={v4()} index={index+1}/>
                    )
                }) : <h1 className="nodata"> Bannerlar ro'yxati hali bo'sh </h1>
            }
        </div>
    )
}

function ListItem({item , index}){

    const [load , setLoad] = useState(false);
    const dispatch = useDispatch();

    function deleteSlide(id){
        setLoad(true);
        api.sliderApi.deleteSlide(id)
            .then(()=>{
                dispatch(SliderActions.getSliders())
            })
            .finally(()=>{
                setLoad(false)
            })
    }

    return(
        <div className="body">
            <div> {index} </div>
            <div> <img src={`http://app.partsmart.uz:8001/files/${item.image}`} alt={item.image}/> </div>
            <div> <a href={item.url} target="_blank"> {item.url} </a> </div>
            <div>
                <Switch value={item.status}/>
            </div>
            <div>
                {
                    load ? "Kuting..." : <button onClick={()=>deleteSlide(item.id)}> O'chirish </button>
                }
            </div>
        </div>
    )
}