import { useEffect } from 'react';
import st from './button.module.scss';
import cl from 'classnames';
import { Link } from 'react-router-dom';


function Button({disabled = false , link , to , className , toggle="", icon , flat, htmlType="button", title , onClick , iconAlign , type = "" , block}){

    return(
        <button disabled={disabled} data-toggle={toggle} type={htmlType} onClick={onClick} className={cl(className ? className : "",st.button, type === "primary" ? st.button_primary : type === "danger" ? st.button_danger : "" , block ? st.button_block : type === "flat" ? st.button_flat : "" , flat && st.button_transparent )}>
            {
                !link ?
                <>
                {
                icon && iconAlign && iconAlign === "left" ?
                <>
                    {icon}  { title }
                </>
                : icon ?
                <>
                    { title } {icon}  
                </>
                : title
            }</> : 
            <Link to={to}>
                {
                    icon && iconAlign && iconAlign === "left" ?
                    <>
                        {icon}  { title }
                    </>
                    : icon ?
                    <>
                        { title } {icon}  
                    </>
                    : title
                }
            </Link>
            }
        </button>
    )
}

export default Button;