import { createReducer } from '@reduxjs/toolkit';
import { SliderActions } from '../actions';

const initialState = {
    pending : true,
    failed : true,
    list : []
}

const SliderReducer = createReducer(initialState , builder => {
    builder
        .addCase(SliderActions.getSliders.fulfilled , (state, action) =>{
            return {...state , pending : false , list : action.payload}
        })
})

export default SliderReducer;