import React , { useEffect, useState } from 'react';
import './input.scss';
import  { v1 } from  'uuid'


export default function Input({ className = '' ,name ,id , type , label , value , handleChange , error = ''}) {
    
    const [initial , setInitial] = useState(v1())
    const [filename , setFilename] = useState('Fayl tanlanmagan')

    return(
        <div className={`input ${className}`}>
            <label htmlFor={id || initial}>
                { label || 'Label' }
            </label>
            {
                type !== 'file' ? <input
                name={name || initial}
                type={type || 'text'}
                id={ id || initial }
                value={value}
                onChange={ e => handleChange(e)}/>
                :
                <>
                    <input
                        name={name || initial}
                        type='file'
                        style={{display : "none" , width : "0px"}}
                        id={ id || initial }
                        value={value}
                        onChange={ e => {
                            setFilename(e.target.files[0].name);
                            handleChange(e)
                        }}/>
                    <label htmlFor={id || initial} className="fileContainer">
                        { filename }
                    </label>
                </>
            }
            { error !== '' && <p className="error">
                { error }
            </p> }
        </div>
    )
}


