import { Children, useEffect, useState } from 'react';
import st from './header.module.scss';
import cl from 'classnames';
import logo from '../../assets/images/logo.jpg';
import { FiUser , FiLogOut , FiPackage , FiMenu} from 'react-icons/fi'
import { Button , Sidebar} from '../../components';
import Waves from 'node-waves';


function Header({children}){

    useEffect(()=>{
        Waves.init();
        Waves.attach("a.dropdown-item",["waves-effect","waves-block"])
    },[])
    const [win , setWin] = useState(window.innerWidth < 990);
    const [sidebarOpen , setSidebarOpen] = useState(win ? false : true);
    return(
        <>
            <header className={cl(st.header , sidebarOpen ? st.header_padded : "")}>
                <Button onClick={ () => {
                    if(win){
                        setSidebarOpen(true)
                    }else{
                        setSidebarOpen(!sidebarOpen)
                    }
                }} flat={true} icon={<FiMenu color="#A21B29"/>}/>
                <div className={st.header_logo}>
                    <img src={logo} alt="gmparts"/>
                </div>
                <div className="dropdown">
                    <Button flat={true} className="dropdown-toggle" icon={<FiUser color="#A21B29"/>}  toggle="dropdown"/>
                    <div className="dropdown-menu dropdown-menu-right">
                        <a href="#" className="dropdown-item" onClick={()=>{
                            localStorage.removeItem("user");
                            window.location.reload();
                        }}><FiLogOut/> Chiqish </a>
                        <a href="#" className="dropdown-item"><FiPackage/> Profil </a>
                    </div>
                </div>
            </header>
            <Sidebar onChange={ val => setSidebarOpen(val)} open={sidebarOpen}/>
            <div className={cl(st.content , sidebarOpen ? st.content_padded : st.content_hided)}>
                { children }
            </div>
        </>
    )
}

export default Header;