import React, { useEffect , useState } from 'react';
import './select.scss';
import { v4 } from 'uuid';

function Select({items  , defaultVal = "",error , id = v4() ,label = "Select" , name = v4(),disabled = false, handleChange = (value) => console.log(value)}) {
   
    return(
        <div className="selectList">
            <label htmlFor={id}> {label} </label>
            <select disabled={disabled}  defaultValue="-1" onChange={ e => {
                handleChange(e.currentTarget.value)
            }} name={name} id={id}>
                <option value="-1" disabled={true}> Tanlanmagan </option>
                {
                    items.map( item => {
                        return(
                            <option key={`${item.label}${item.value}`} value={item.value}> {item.label} </option>
                        )
                    })
                }
            </select>
            {
                error && 
                <p className="error">
                    { error }
                </p>
            }
        </div>
    )
}

export default Select;